.Search-container{
display: flex;
justify-content: center;
margin: 10px;
}
.Search-input{
    padding: 8px 15px;
    font-size: 15px;
    width: 330px;
    border-width: 0;
    background: rgb(190, 186, 172);
    color: rgb(1, 252, 114);
border-radius: 70px;
box-shadow: 0 0 5px rgba(117, 255, 4, 1);
}
.Search-input::placeholder{
    color: seagreen;
}
.Search-input:focus{
    outline: none;


}