.result-name-card{
    color: #20ff02;
    background-color: #0d191b;
    margin: 10px 15px;
    min-width: 75px;
    padding: 10px 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 5px rgba(255, 255, 255, 0.2);
    transition: background 0.35s;
    display: flex;
    justify-content: center;
}
.card-link{
    text-decoration: none;
}

.result-name-card:hover{
    background: #4a5079;
    box-shadow: 0 0 5px rgb(231, 5, 5);
}
.result-name{
    margin: 0;
    font-weight: 100;
}